body {
  font-family: 'Courier New', Courier, monospace;
  background-color: black;
  background-image: url('media/back.jpg');
  background-size: auto;
  background-repeat: no-repeat;
}
.App {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  background-color: black;
  width: 25%;
}
p {
  color: white;
}
h1 {
  font-style: italic;
  color: aquamarine;
}
/* .line {
  background-color: white;
  width: 100%;
  color: white;
  margin-top: 27rem;
} */
.line {
  width: 100%;
  height: 0.3rem;
  display: inline-block;
  position: relative;
  background: rgba(255, 255, 255, 0.2);
  overflow: hidden;
  margin-top: 5.8rem;
}
.line::after {
  content: '';
  width: 2rem;
  height: 0.3rem;
  background: aquamarine;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  animation: animloader 1s linear infinite;
}

@keyframes animloader {
  0% {
    left: 0;
    transform: translateX(-100%);
  }
  100% {
    left: 100%;
    transform: translateX(0%);
  }
}

.social-container {
  text-align: center;
}
.fa-bandcamp {
  margin-right: 1rem;
  color: aquamarine;
}
.fa-facebook {
  margin-right: 1rem;
  color: aquamarine;
}

.fa-instagram {
  color: aquamarine;
}

@media only screen and (max-width: 600px) {
  /* MOBILE */
  body {
    background-size: contain;
    background-position-x: center;
  }
  .App {
    background-color: rgba(0, 0, 0, 0.85);
    border: 1px solid aquamarine;
    margin-top: 3rem;
    width: 90%;
    padding-bottom: 5rem;
  }
  p {
    border: 1px solid aquamarine;
    width: 50%;
    padding: 0.2rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1rem;
  }
}

@media only screen and (min-width: 600px) and (max-width: 1440px) {
  /* Tablet and smaller monitors */
  body {
    background-position-x: center;
  }
  .App {
    background-color: rgba(0, 0, 0, 0.6);
    width: 50%;
    padding-bottom: 3rem;
    margin-bottom: 0;
  }
  p {
    background-color: rgba(0, 0, 0, 0.6);
    border: 1px solid aquamarine;
    width: 50%;
    padding: 0.2rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1rem;
  }
  .line {
    margin-top: 10%;
  }
}
